mEM,cACA,gBACA,cACA,SAAU,CAtEhB,8DAyEM,cACA,eACA,cACA,yBAA0B,CA5EhC,uDA+EM,eACA,iBAAkB,CAClB,oCAjFN,uDAkFQ,aAAc,CAEjB,CApFL,uDAuFI,kBACA,WACA,WAAY,CAzFhB,yDA2FQ,UAAW,CACZ,oCA5FP,kCAgGM,sBACA,aAAc,CAmBjB,CAjBC,oCAnGJ,kCAoGM,sBACA,cAAe,CAelB,CAbC,0DAvGJ,kCAwGM,sBACA,cAAe,CAWlB,CATC,oCA3GJ,kCA4GM,qBAAsB,CAQzB,CANC,oCA9GJ,kCA+GM,cAAe,CAKlB,CAHC,oCAjHJ,kCAkHM,cAAe,CAElB,CAGH,wBACE,gBACA,aACA,sBACA,2BACA,UACA,YACA,eAAgB,CAEhB,oCATF,wBAUI,aACF,CA+CD,CA1DD,4BAcI,cACA,gBACA,kBACA,oBACA,8BACA,aACA,iBAEA,kBAAmB,CAtBvB,iCAyBM,iBACA,cACA,cACA,gBACA,iBACA,qBACA,gBAAiB,CACjB,oCAhCN,iCAiCQ,gBAAiB,CAMpB,CAJC,oCAnCN,iCAoCQ,kBACA,eAAgB,CAEnB,CAvCL,gKA0CM,aAAc,CA1CpB,4NA4CQ,wBAAyB,CA5CjC,yCAiDM,SACA,eAAgB,CAlDtB,8BAsDM,cACA,qBAAsB,CACvB,OAKH,mBACA,YACA,kBACA,cAAe,CAJjB,cAOM,aACA,cAAe,CAChB","file":"styles.scss","sourcesContent":["$color-dark-blue: #2B455F;\n$color-pink: #e21d62;\n$color-underline: #f36287;\n$color-light-blue: #c9dff7;\n$color-blue: #5f8fc6;\n\n$color-instagram: #cd486b;\n$color-smoke-white: #d8dbdc;\n$color-white: #F8FBFC;\n\nhtml {\n  @media screen and (max-width: 768px) {\n    font-size: 80%;\n  }\n  @media screen and (max-width: 500px) {\n    font-size: 60%;\n  }\n  @media screen and (max-width: 400px) {\n    font-size: 50%;\n  }\n  @media screen and (max-width: 320px) {\n    font-size: 48%;\n  }\n}\n\nbody {\n  box-sizing: border-box;\n  padding: 0rem;\n  margin: 0;\n  //background: #2B455F;\n  font-family: 'Nunito', sans-serif;\n}\n\na {\n  text-decoration: none;\n  color: $color-white;\n}\n\nul {\n  padding: 0;\n  list-style: none;\n}\n\n.download_cv {\n  padding: 1em 3em;\n  border-radius: 2em;\n  background: #cd486b;\n  color: #fff;\n  font-size: 14px;\n  box-shadow: 0px 0px 5px #2b2b2b;\n  text-shadow: none;\n  &:hover {\n    box-shadow: 0px 0px 20px 5px #2b2b2b;\n  }\n}\n\n.desc__list {\n  list-style: disc;\n  padding: 0.5em 2em;\n}\n\nsection {\n  scroll-behavior: smooth; /* <----- THE SECRET ---- */\n}\n\n.last--updated {\n  position: absolute;\n  bottom: 30px;\n  right: 30px;\n  color: #fff;\n  font-size: 14px;\n}\n\n\n.content {\n  background: $color-dark-blue;\n  color: $color-smoke-white;\n  //border-radius: 0.5em;\n  box-shadow: 7px 7px 120px 0px #3c3c3c;\n\n  display: flex;\n  flex-direction: column;\n\n  > .particles--container {\n    width: 100%;\n    z-index: 0;\n    position: absolute;\n    height: 100%;\n    top: 0;\n    left: 0;\n    > canvas {\n      height: calc(100vh - 12rem);\n    }\n  }\n  .header-bar {\n    z-index: 99;\n    padding: 1em 2em;\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: stretch;\n\n    @media screen and (max-width: 900px) {\n      flex-direction: column;\n      padding: 1em;\n    }\n\n    .header-bar--links {\n      flex: 3;\n      > .header-bar--links__list {\n        display: flex;\n        justify-content: flex-end;\n        flex-direction: row;\n        flex-wrap: wrap;\n        > .header-bar--links__item {\n          font-size: 1.2em;\n          padding: 0.5em 0em;\n          text-transform: uppercase;\n          display: inline-block;\n          text-shadow: 1px 1px 10px #222;\n          > a {\n            padding: 0.5em 1em;\n          }\n          &:hover {\n            background: #d0d0d087;\n            border-radius: 4em;\n          }\n          @media screen and (max-width: 900px) {\n            font-size: 1em;\n          }\n          @media screen and (max-width: 500px) {\n            font-size: 1.4em;\n          }\n        }\n      }\n    }\n\n    > .social--icons {\n      flex: 1;\n      z-index: 99;\n      display: flex;\n      flex-direction: row;\n      justify-content: flex-end;\n      align-items: center;\n      > .social--icon__item {\n        font-size: 30px;\n        margin: 1rem;\n        &:hover {\n          > a {\n            > .fa-github {\n              color: #777777;\n            }\n            > .fa-instagram {\n              color: #fb92ae;\n            }\n            > .fa-linkedin {\n              color: #5fbaea;\n            }\n          }\n        }\n      }\n    }\n  }\n\n  > .heading--title {\n    padding: 4rem 10rem;\n    min-height: calc(100vh - 12rem);\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    transition: all 0.5s ease-in-out;\n    box-shadow: 0px 10px 20px #4e4e508c;\n    z-index: 2;\n    background-image: url('/assets/media/bg.png');\n    background-size: cover;\n    background-repeat: no-repeat;\n    position: relative;\n\n    > .main--heading {\n      font-size: 4em;\n      color: #F8FBFC;\n      margin-bottom: 0rem;\n    }\n    > .sub--heading {\n      font-size: 2.2em;\n      text-shadow: 4px 4px 15px #292929;\n      color: #ff84a5;\n      letter-spacing: 1px;\n      //color: $color-instagram;\n    }\n    > .description {\n      font-size: 1.5em;\n      color: $color-light-blue;\n      text-shadow: 4px 4px 15px #171717;\n      padding-bottom: 30px;\n\n      @media screen and (max-width: 500px) {\n        font-size: 1.8em;\n      }\n    }\n\n    @media screen and (max-width: 900px) {\n      padding: 4rem 4rem;\n    }\n    @media screen and (max-width: 700px) {\n      padding: 4rem 5rem;\n    }\n    @media screen and (max-width: 400px) {\n      padding: 4rem 5rem 0;\n    }\n  }\n  > .light--content {\n\n    border-radius: 0 0 0.5em 0.5em;\n    background: #f2f3f3;\n    padding: 2em 1em 4em;\n\n    > .content--title {\n\n      position: relative;\n      color: $color-dark-blue;\n      font-size: 30px;\n      text-align: center;\n      padding-bottom: 0.5em;\n\n      &:after {\n        content: '';\n        position: absolute;\n        bottom: 0;\n        left: 50%;\n        width: 150px;\n        border-radius: 4px;\n        height: 4px;\n        transform: translateX(-50%);\n        background: $color-underline;\n      }\n    }\n  }\n  > .dark--content {\n\n    border-radius: 0 0 0.5em 0.5em;\n    background: #103152;\n    padding: 2em 1em 4em;\n\n    > .content--title {\n\n      position: relative;\n      color: $color-white;\n      font-size: 30px;\n      text-align: center;\n      padding-bottom: 0.5em;\n\n      &:after {\n        content: '';\n        position: absolute;\n        bottom: 0;\n        left: 50%;\n        width: 150px;\n        border-radius: 4px;\n        height: 4px;\n        transform: translateX(-50%);\n        background: #89ba16;\n      }\n    }\n  }\n}\n\n.timeline--container {\n  overflow: hidden;\n\n  > ul.timeline--line {\n    padding: 0;\n\n    &.dark {\n      > li {\n        box-shadow: 2px 2px 7px rgb(8, 26, 67);\n        &::after {\n          background: $color-light-blue;\n          box-shadow: 2px 2px 7px rgba(80, 79, 79, 0.5);\n        }\n        &.in-view {\n          &:after {\n            background: $color-light-blue;\n          }\n        }\n        > div.timeline_item--content {\n          box-shadow: 2px 2px 7px rgb(8, 26, 67);\n        }\n      }\n    }\n\n    > li {\n      list-style-type: none;\n      position: relative;\n      width: 6px;\n      margin: 0px auto 0;\n      padding-top: 50px;\n      border: 1px solid;\n      border-radius: 3px;\n      background: #f5f5f5;\n      box-shadow: 2px 2px 7px rgb(199, 197, 197);\n\n      &::after {\n        content: '';\n        position: absolute;\n        left: 50%;\n        bottom: 0;\n        transform: translateX(-50%);\n        width: 30px;\n        height: 30px;\n        border-radius: 50%;\n        background: $color-pink;\n        border: 1px solid;\n        box-shadow: 2px 2px 7px rgba(214, 212, 212, 0.5);\n\n        background: #fff;\n        transition: background .5s ease-in-out;\n      }\n\n      &:nth-child(odd) div.timeline_item--content {\n        left: 45px;\n        transform: translate3d(200%, 0, 0);\n      }\n\n      &:nth-child(odd) div.timeline_item--content::before {\n        left: -15px;\n        border-width: 10px 15px 10px 0;\n        border-color: transparent #F45B69 transparent transparent;\n      }\n\n      &:nth-child(even) div.timeline_item--content {\n        transform: translate3d(-200%, 0, 0);\n        left: -45px;\n        @media screen and (max-width: 700px) {\n          left: 45px;\n          transform: translate3d(200%, 0, 0);\n        }\n      }\n\n      &:nth-child(even) div.timeline_item--content::before {\n        right: -15px;\n        border-width: 10px 0 10px 15px;\n        border-color: transparent transparent transparent #F45B69;\n        @media screen and (max-width: 700px) {\n          left: -15px;\n          border-width: 8px 16px 8px 0;\n          border-color: transparent #F45B69 transparent transparent;\n        }\n      }\n\n      > div.timeline_item--content {\n\n        visibility: hidden;\n        //display: none;\n        opacity: 0;\n        transition: all 0.8s ease-in-out;\n\n        position: relative;\n        bottom: 0;\n        width: auto;\n        min-width: 200px;\n        padding: 1em 2em;\n        background: #FFFFFF;\n        color: $color-white;\n        border-radius: 0.5em;\n        border: 1px solid #dcdcdc;\n        box-shadow: 4px 4px 20px #dcdcdc;\n\n        > .timeline_item--title {\n          color: $color-pink;\n          font-size: 20px;\n          margin-bottom: 0.5em;\n        }\n\n        > .timeline_item--sub_title {\n          color: $color-dark-blue;\n          font-size: 16px;\n          margin-bottom: 0.9em;\n\n          > .timeline_item--time {\n            margin-top: 0.5em;\n            font-size: 12px;\n            text-transform: uppercase;\n            color: #865a27;\n          }\n        }\n\n        > .timeline_item--desc {\n          color: #333;\n          font-size: 1em;\n          @media screen and (min-width: 900px) {\n            font-size: 0.9em;\n            overflow: scroll;\n          }\n          @media screen and (max-width: 899px) {\n            font-size: 1em;\n          }\n          @media screen and (max-width: 500px) {\n            font-size: 1.6em;\n            overflow: scroll;\n          }\n        }\n        @media screen and (min-width: 900px) {\n          width: calc(50vw - 150px);\n        }\n        @media screen and (min-width: 701px) and (max-width: 900px) {\n          width: calc(50vw - 130px);\n        }\n        @media screen and (max-width: 700px) {\n          width: calc(100vw - 200px);\n        }\n        @media screen and (max-width: 500px) {\n          width: calc(100vw - 140px);\n        }\n      }\n\n      > div.timeline_item--content::before {\n        content: '';\n        position: absolute;\n        bottom: 7px;\n        width: 0px;\n        height: 0;\n        border-style: solid;\n      }\n\n      @media screen and (max-width: 700px) {\n        margin-left: 20px;\n      }\n    }\n    > li.in-view {\n      &:after {\n        background: #F45B69;\n      }\n      &:nth-child(odd) div.timeline_item--content {\n        transform: none;\n        visibility: visible;\n        opacity: 1;\n        display: block;\n\n      }\n\n      &:nth-child(even) div.timeline_item--content {\n        transform: translateX(-100%);\n        visibility: visible;\n        opacity: 1;\n        display: block;\n        @media screen and (max-width: 700px) {\n          transform: translateX(0%);\n        }\n      }\n    }\n  }\n}\n\n.content--body {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  align-items: flex-start;\n  padding: 4em 2em;\n\n  > .col {\n    flex: 1;\n    margin: 10px;\n    > h2 {\n      text-align: center;\n      color: #fff;\n      letter-spacing: 1px;\n    }\n  }\n\n  @media screen and (max-width: 800px) {\n    flex-direction: column;\n    > .col {\n      width: 100%;\n      > h2 {\n        font-size: 2em;\n      }\n    }\n  }\n}\n\n.skillbar {\n  position: relative;\n  display: block;\n  margin-bottom: 2.5em;\n  width: 90%;\n  background: #eee;\n  height: 35px;\n  border-radius: 3px;\n  transition: 0.4s linear;\n  transition-property: width, background-color;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n.skillbar-title {\n  position: absolute;\n  top: 0;\n  left: 0;\n  font-weight: bold;\n  font-size: 13px;\n  color: #fff;\n  background: #6adcfa;\n  border-top-left-radius: 3px;\n  border-bottom-left-radius: 3px;\n}\n\n.skillbar-title span {\n  display: block;\n  background: rgba(0, 0, 0, 0.2);\n  padding: 0 20px;\n  height: 35px;\n  line-height: 35px;\n  border-top-left-radius: 3px;\n  border-bottom-left-radius: 3px;\n}\n\n.skillbar-bar {\n  height: 35px;\n  width: 0px;\n  background: #6adcfa;\n  border-radius: 3px;\n}\n\n.skill-bar-percent {\n  position: absolute;\n  right: 10px;\n  top: 0;\n  font-size: 11px;\n  height: 35px;\n  line-height: 35px;\n  color: #444;\n  color: rgba(0, 0, 0, 0.4);\n}\n\n.projects--content {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  justify-content: space-between;\n  align-content: space-between;\n  padding: 4em 2em;\n  color: #333;\n\n  > .project--item {\n    width: calc(28% - 2em);\n    padding: 1em;\n    margin: 1em;\n    min-height: 150px;\n    border: 1px solid #ccc;\n    background-color: #fff;\n    border-radius: 8px;\n    transition: all 0.2s ease-in-out;\n    position: relative;\n\n    &.react {\n      background-image: url(https://upload.wikimedia.org/wikipedia/commons/thumb/a/a7/React-icon.svg/1024px-React-icon.svg.png);\n      background-size: 70px;\n      background-repeat: no-repeat;\n      background-position: 100% 10px;\n    }\n\n    &.python {\n      background-image: url(https://upload.wikimedia.org/wikipedia/commons/c/c3/Python-logo-notext.svg);\n      background-size: 40px;\n      background-repeat: no-repeat;\n      background-position: 95% 10px;\n    }\n\n    &.siebel {\n      background-image: url(https://upload.wikimedia.org/wikipedia/commons/7/71/Siebel_Systems_logo.svg);\n      background-size: 60px;\n      background-repeat: no-repeat;\n      background-position: 95% 30px;\n    }\n\n    &.android {\n      background-image: url(https://upload.wikimedia.org/wikipedia/commons/d/d7/Android_robot.svg);\n      back